import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3, 2),
  },
}));

export default function PaperSheet() {
  const classes = useStyles();

  return (
    <div>
      <Paper className={classes.root}>
        <Typography variant="h5" component="h1">
          PyTorch Tutorial (11/13/2018)
        </Typography>
        <Typography component="p">
        <br />Annotating a tutorial on PyTorch and basics of Deep Learning. ReLu is a rectified linear unit. It is a type of activation function( Activation functions maps inputs that aren't neccessarily probabilistic to probabilistic values which are values either from -1 to 1 or 0 to 1. Activation functions such as ReLu...<br /> <a href="https://nbviewer.jupyter.org/github/knjiang/Learning-Deep-Learning/blob/master/PyTorchNNTutorial_W_Notes.ipynb">More...</a>
        </Typography>
      </Paper>
    </div>
  );
}